@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;1,100&display=swap');


.blog-part{
    text-align: center;
    padding: 65px;
}


.text-blog .heading-blog h2{
    font-size: 30px;
    margin-bottom: 8px;
    line-height: 1.2;
    font-family: 'Poppins', sans-serif;
}


.text-blog .title-blog h6{
    font-size: 15px;
    margin-bottom: 8px;
    line-height: 1.2;
    color: #6e6e6e;
    font-family: 'Poppins', sans-serif;
}


.card-part-blog {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    padding: 10px;
}



.card-part-blog .card{    
    padding: 75px 0;   
    width: 350px;
    height: 85%;
     
}


.card .img-case{
    transition: all .8s ease-in-out;
    cursor: pointer;
}

.card .img-case:hover{
    transform: scale(1.1);
}


.card-part-blog .blue{
    background-color: #000637;
    transition: all .8s ease-in-out;
}


.card-part-blog .yellow{
    background-color: #ffad00;
    transition: all .8s ease-in-out;
}

.card-part-blog .purple{
    background-color: #bbbbf2;
    transition: all .8s ease-in-out;
}

.card-part-blog .green{
    background-color: #5ed6b3;
    transition: all .8s ease-in-out;
}

.card-blog h6{
    font-size: 17px;
    padding: 10px;
    padding-bottom: 4px;
    font-weight: 800;
    line-height: 1.2;
    font-family: 'Poppins', sans-serif;
}




@media screen and (min-width:0px) and (max-width:576px){
    .card-part-blog {
        display: inline;
        grid-template-columns: repeat(4, auto);
        grid-gap: 10px;
        padding: 5px;
    }

    .card-part-blog .card{    
        padding: 75px 0;   
        width: 250px;
        height: 85%;        
    }

    .text-blog .heading-blog h2{
        font-size: 30px;
        margin-bottom: 8px;
        line-height: 1.2;
        font-family: 'Poppins', sans-serif;
        
    }
    
    
    .text-blog .title-blog h6{
        font-size: 15px;
        margin-bottom: 8px;
        line-height: 1.2;
        color: #6e6e6e;
        font-family: 'Poppins', sans-serif;
       
    }
    
}


@media screen and (min-width:768px) and (max-width:1200px){
    .card-part-blog {
        display:grid;
        grid-template-columns: repeat(2, auto);
        grid-gap: 10px;
        padding: 10px;
          
    }


    .card-part-blog .card{    
        padding: 75px 0;   
        width: 300px;
        height: 85%;        
    }
}

